'use client';

import React from 'react';
import Link from 'next/link';
import { Home, User, BarChart3, BrainCircuit, Settings } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { useModalStore } from '@/store/useModalStore';
import { useAuthStore } from '@/store/useAuthStore';

export function Header() {
  const { openAuthModal } = useModalStore();
  const { user, logout } = useAuthStore();
  return (
    <header className="bg-background flex items-center justify-between border-b px-6 py-4">
      <div className="flex items-center gap-4">
        <Link href="/" className="text-xl font-bold">
          TypeAmp
        </Link>
      </div>

      <nav className="flex items-center gap-6">
        <Link
          href="/"
          className="text-muted-foreground hover:text-foreground flex items-center gap-2 transition-base"
        >
          <Home className="h-4 w-4" />
          <span>Home</span>
        </Link>
        <Link
          href="/profile"
          className="text-muted-foreground hover:text-foreground flex items-center gap-2 transition-base"
        >
          <User className="h-4 w-4" />
          <span>Profile</span>
        </Link>
        <Link
          href="/stats"
          className="text-muted-foreground hover:text-foreground flex items-center gap-2 transition-base"
        >
          <BarChart3 className="h-4 w-4" />
          <span>Stats</span>
        </Link>
        <Link
          href="/learn"
          className="text-muted-foreground hover:text-foreground flex items-center gap-2 transition-base"
        >
          <BrainCircuit className="h-4 w-4" />
          <span>Learn</span>
        </Link>
        <Link
          href="/customize"
          className="text-muted-foreground hover:text-foreground flex items-center gap-2 transition-base"
        >
          <Settings className="h-4 w-4" />
          <span>Customize</span>
        </Link>
      </nav>

      {user ? (
        <div className="flex items-center gap-4">
          <span className="text-muted-foreground text-sm">{user.email}</span>
          <Button variant="outline" onClick={logout}>
            Logout
          </Button>
        </div>
      ) : (
        <Button variant="outline" onClick={openAuthModal}>
          Login
        </Button>
      )}
    </header>
  );
}
